.mainpara{
    padding : 2px 30px;
}

.icondivpad{
    padding: 0px 20px;
    text-align: center;
}

.maincap {
    color: #002c5cd1;
    margin-bottom: 30px;
    font-size: 80px;
    /* text-transform: uppercase; */
    letter-spacing: .1em;
    position: relative;
    display: inline-block;
    font-weight: 300;
    font-family: great;
}

.servicehead {
    color: #585252;
    margin-bottom: 15px;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: .1em;
    position: relative;
    display: inline-block;
    font-weight: 300;
    font-family: oswald;
}

.servicehead::after{
    width: 60px;
    height: 2px;
    background: rgb(205, 160, 95);
    position: absolute;
    left: 105%;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    content: "";
}

.servicetab {
    width: 100%;
    text-align: left;
    padding: 21px 30px;
    background-color: transparent;
    border: 1px solid #fff;
    margin: 1px 0px;
}

.servicetab:hover {
    background-color:rgba(255, 255, 255, 0.87) !important;
    -webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
}

.servicetab:hover p{
    color: rgb(205, 160, 95) !important;
    -webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
}

.servicetab:focus p{
    color: rgb(205, 160, 95) !important;
    -webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
}

.servicetab:focus {
    background-color:rgba(255, 255, 255, 0.87) !important;
    -webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
}

.rwt__tablist[aria-orientation="vertical"] .rwt__tab[aria-selected="true"]:after{
    border-right: 7px solid rgb(205, 160, 95) !important;
}

.servicetabcontent {
    padding: 50px 80px;
     background-color:rgba(72, 72, 72, 0.39);
     height: 410px;
}

.servicemaindiv {
    background-size: cover;
    background-position: left center;
    background-repeat: no-repeat;
    position: relative;
    background-image: url('/images/homeservicebg.jpg');
    padding: 90px 90px;
    background-attachment: fixed;
}

.servicedivoverlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
}

.hometablist{
    width: 33%;
}

.designbg {
    background: url('https://360.agencewebcom.com/web/uploads/api/cache/site-302/svg/original//0x0/le-six-hotel-reglages-68754.svg') no-repeat;
    background-size: 149px 130px;
    background-position: calc(100% - -15px) 20px;
    padding-left: 10%;
    padding-right: 10%;
}
.servicedivideddiv{
    border-radius: 5px;
    padding: 5px 15px;
}

.servicemainmobile{
    display: none;
}

@media only screen and (max-width: 667px) {
    .servicemaindiv{
      display: none;
    }
    .servicemainmobile{
        display: block;
    }
    .designbg {
        background: none;
    }
}



