.mapselect{
    background-color : rgb(18, 181, 149);
    height: 60px;
    padding: 15px 0px;
    text-align: center;
    font-size: 1.1em;
    font-weight: 500;
    border-right: 1px solid #fff;
    text-transform: uppercase;
    /*border-radius: 5px;*/
}

.mapunselect{
    background-color : #002c5cd1;
    height: 60px;
    padding: 15px 0px;
    text-align: center;
    font-size: 1.1em;
    font-weight: 500;
    border-right: 1px solid #fff;
    text-transform: uppercase;
    /*border-radius: 5px;*/
}


.mapselect a{
    color:#fff;
    cursor:pointer;
    font-size:1.3em;
    font-family:DINLight;
}

.mapunselect a{
    color:#fff;
    cursor:pointer;
    font-size:1.3em;
    font-family:DINLight;
}

@media only screen and (max-width: 667px) {
	.mapattract{
        display: none !important;
    }
    .mapselect a{
        font-size:1.1em;
    }
    .mapunselect a{
        font-size:1.1em;
    }
}



