.bookinglogo{
    display: inline;
    background-color:#3b4868;
    border: 1px solid #656e82;
    -webkit-box-shadow: 0.5px 1.5px 0.5px 0.5px #6e727b;
            box-shadow: 0.5px 1.5px 0.5px 0.5px #6e727b;
    top:-15px;
    height:130px;
    margin-top: 30px;	
    width:250px;
    padding: 12px 83px;
}

.mobileclose{
    display: none;
    font-size: 35px;
}

.formheadtext{
    display: none;
}

.formheaddiv{
    background-color: rgb(255, 255, 255);
    border: none;
    min-height: 100px;
} 

@media only screen and (max-width: 667px) {
	.mobileclose{
        display: block;
    }
    .bookinglogo{
        display: none;
    }
    .formheadtext{
        display: block;
    }
    .formheaddiv{
        min-height: 150px;
    } 
}


