
.booknowmobile{
    position: fixed;
    height: 50px;
    bottom: 0;
    left: 50%;
    -webkit-transform: translatex(-50%);
        -ms-transform: translatex(-50%);
            transform: translatex(-50%);
    width: 100%;
    background-color: #e4572e;
    color: white;
    text-align: center;
    z-index: 999;
    padding: 10px;
}

.mobilenavbar {
    height: 60px;
    position: fixed;
    background-color: rgb(255, 255, 255);
    z-index: 999;
    width: 100%;
    padding: 10px 15px;
    -webkit-box-shadow: 0 0 2px 0 rgba(0,0,0,.2);
    box-shadow: 0 0 2px 0 rgba(0,0,0,.2);
}


.mobilenavimg {
    position: absolute;
    left: 50%;
    -webkit-transform: translatex(-50%);
        -ms-transform: translatex(-50%);
            transform: translatex(-50%);
}

.overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #fff;
    overflow-x: hidden;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    webkit-box-shadow: 0 0 10px 0 rgba(0,0,0,.2);
    -webkit-box-shadow: 0 0 10px 0 rgba(0,0,0,.2);
            box-shadow: 0 0 10px 0 rgba(0,0,0,.2);
}

.overlay-content {
    position: relative;
    top: 8%;
    width: 100%;
    padding: 0px 40px;
}

.overlay a {
    padding: 10px 0px;
    text-decoration: none;
    font-size: 20px;
    color: #000;
    display: block;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
    color: rgb(205, 160, 95);
}

.overlay .closebtn {
    position: absolute;
    right: 25px;
    font-size: 30px;
    color: #cda05f;
}

