
.webhomeservice{
    display : block;
}

.mobilehomeservice{
    display: none;
  }

.servediv{
    min-height: 400px; 
    padding: 0%;
    overflow: hidden;
}

.servedivimg{
    height: 400px; 
    width: 100%;
}

.serviceimgopacity{
    position: absolute;
    padding: 2% 2%;
    z-index: 15;
    width: 100%;
    left:0%;
    background: -webkit-linear-gradient(100deg, #12509473, #75222263);
    background: -o-linear-gradient(100deg, #12509473, #75222263);
    background: linear-gradient(350deg, #12509473, #75222263);
    height: 400px;
    overflow: hidden;
    cursor: pointer;
}

.contactback{
    width: 100%;
    height: 500px;
}

.servicemove{
    margin-top: 300px;
    padding: 3%;
}

.serviceimgopacity:hover{
    background-color: #002c5ca1;
}

.serviceimgopacity:hover .servicemove{
    margin-top: 100px;
    margin-left: 20px;
    border-left: 2px solid #fff;
    -webkit-transition: all 0.25s ease-in;
    -o-transition: all 0.25s ease-in;
    transition: all 0.25s ease-in;
    text-decoration: none !important;
}

.serviceimgopacity:hover .servicep{
    margin-top: 10px;
    -webkit-transition: all 0.25s ease-in;
    -o-transition: all 0.25s ease-in;
    transition: all 0.25s ease-in;
    text-decoration: none !important;
}

.servicebtnmove{
    background-color: transparent !important;
    color: #fff !important;
    border: 2px solid #fff !important;
    font-size: 0.8em;
    padding: 3% 8%;
}

.serviceh{
    font-size: 1.8em;
    color: #fff;
    text-transform: uppercase;
}

.servicep{
    margin-top: 50px;
    color: #fff;
}

.servicereadmore {
    width: 150px;
    background-color: #fff;
    color: #e4572e;
    font-size: 1em;
    /* border-radius: 50px; */
    height: 50px;
    border: 2px solid #e4572e;
    text-transform: uppercase;
}

.servicereadmore:hover {
    background-color: #e4572e;
    color: #fff;
    font-size: 1em;
    -webkit-transition: all 0.25s ease-in;
    -o-transition: all 0.25s ease-in;
    transition: all 0.25s ease-in;
}

.serviceimage {
    background-size: cover;
    background-position: left center;
    background-repeat: no-repeat;
    position: relative;
}

.serviceimg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.1);
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.servicerow:hover .serviceimg{
    background: rgba(0, 0, 0, 0.3);
    border:5px solid #fff;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}


.servicerow:hover .serviceimage{
    transform: scale(1.2);
    -moz-transform: scale(1.2);
    -webkit-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2); /* IE 9 */
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.servicetext {
    /* background-color: #fff; */
    -webkit-box-shadow: rgba(0,0,0,.4) 0 0 5px;
            box-shadow: rgba(0,0,0,.4) 0 0 5px;
    background-color: rgba(255, 255, 255, 0.82);
    height: 350px;
    padding: 5px 50px;
    /* box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 10px 0px; */
}

@media only screen and (max-width: 667px) {
    .webhomeservice{
      display: none;
    }
    .mobilehomeservice{
      display: block;
    }
    .mobileservicedes{
        padding: 10px 20px;
    }
}


