
.transnav .navlink {
    margin-top: 5px;
    position: absolute;
    right: 10%;
    top: 65px;
}

.navbar-fixed-top .navlink {
    margin-top: 15px;
    position: absolute;
    right: 10%;
    top: 15px;
}

.navbar-center {
    margin-top: 40px;
    position: absolute;
    left: 50%;
    -webkit-transform: translatex(-50%);
        -ms-transform: translatex(-50%);
            transform: translatex(-50%);
}

.transnav {
    background-color: transparent;
    border: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    right: 0;
    height: 120px;
    padding: 10px 8%;
    border-bottom: 1px solid #fff;
}

.navname {
    font-size: 1.4em;
    font-family: DINlight;
    line-height: 20px;
    color: #888;
    letter-spacing: 0.02em;
}

.navname:hover {
    color: #12b595;
    -webkit-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
}

.transnav .navname {
    display: none;
}

.navbar-fixed-top{
    background-color: #fff;
    border: none;
    height: 80px;
    padding: 10px 8%;
    -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
}

.transnav .navbar-nav>li>a {
    color: #fff;
    /* text-transform: uppercase; */
    font-family: oswald;
    font-size: 1.2em;
    padding: 10px 5px;
    margin: 0px 10px;
}

.navbar-fixed-top .navbar-nav>li>a {
    color: #000;
    /* text-transform: uppercase; */
    font-family: oswald;
    font-size: 1.2em;
    padding: 10px 5px;
    margin: 0px 10px;
}

.navbar-fixed-top .navphone {
    display: none;
}

.navbar-fixed-top .navimg {
    display: none;
}

.navbar-fixed-top .navbar-nav>li>a:hover {
    color: #000;
}

.navbar-fixed-top .navbar-nav>li>a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #888;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
        transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    -o-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.navbar-fixed-top .navbar-nav>li>a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(0.7);
    -ms-transform: scaleX(0.7);
        transform: scaleX(0.7);
}

.transnav .navbar-nav>li>a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #fff;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
        transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    -o-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.transnav .navbar-nav>li>a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(0.7);
    -ms-transform: scaleX(0.7);
        transform: scaleX(0.7);
}

.transnav .navbar-nav>li>a.active {
    color: #00ffcd;
}

.navbar-fixed-top .navbar-nav>li>a.active {
    color: #12b595;
}

.navbar-brand {
    margin-top: 10px;
}

.reservebtn{
    background-color: #e4572e;
    height: 40px;
    width:150px;
    padding: 0px 0px;
    border-radius: 5px !important;
    color: #ffffff;
    font-size: 0.92em;
    font-weight: 600;
    border-color: transparent !important;
    margin-left: 20px;
    line-height: 8px;
  }

.transnav .reservebtn:hover{
    background-color: #ffffff;
    color: #000;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.navbar-fixed-top .reservebtn:hover{
    background-color: #ffffff;
    border : 2px solid #e4572e !important;
    color: #000;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}


.profilediv {
    border-radius: 50%;
    border: 2px solid #ddd;
}

.profileimg{
    height: 40px;
    width: 40px;
    background-color: rgb(221, 221, 221);
    border-radius: 50%;
}

.dropdown:hover .dropdown-menu {
    display: block;
    margin-top: 2px;
    -webkit-transition: all 0.3s ease-in-out 0s;
    -o-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.dropdown-menu {
    margin: 2px -10px;
    padding: 0;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.dropdown-menu li{
    height: 40px;
    padding: 10px 15px;
}
.dropdown-menu li:hover{
    background-color: #12b595;
}

.dropdown-menu li:hover a{
    background-color: #12b595;
    color: #fff;
}
.dropdown-menu li:hover p{
    background-color: #12b595;
    color: #fff;
}

.dropdown-menu>li>a {
    padding: 0px;
    font-family: Oswald;
}

.navbar-inverse .navbar-toggle {
    border:none;
    margin-top: 15px;
}

.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover {
    background-color: transparent;
}

.navbar-toggle .icon-bar{
    width: 30px;
    height: 3px;
    background-color: #cda05f !important;
}

.navmobile{
    display:none;
}

.navmobile i{
    font-size: 1.5em;
    margin-top: -6px;
    color: #cda05f;
    margin-left: 10px;
}

.navmobileicon{
    display: none;
}
/*****************************************************************************/
/* @media only screen and (max-width: 1300px) {

} */

@media only screen and (max-width: 1200px) {
    .transnav {
        padding: 10px 6%;
    }
    .navbar-fixed-top {
        padding: 10px 6%;
    }
    .transnav .navbar-nav>li>a{
        margin: 0px 12px;
    }
}

@media only screen and (max-width: 1100px) {
    .transnav {
        padding: 10px 4%;
    }
    .navbar-fixed-top {
        padding: 10px 4%;
    }
    .transnav .navbar-nav>li>a{
        margin: 0px 9px;
    }
}

@media only screen and (max-width: 1088px) {

}

/***************Media query for mobile***************/
@media only screen and (max-width: 667px) {
    .navimg{
        display: none !important;
    }
    .transnav{
        background-color: #fff;
        height: 80px;
        padding: 10px 0%;
    }
    .navphone{
        display: none;
    }
    .navbar-collapse{
        border:none;
        background-color: #fff;
    }
    .navmobile{
        display:block;
    }
    .navname{
        display: none;
    }
    .reservebtn{
        display: none;
    }
    .transnav .navbar-nav>li>a {
        color: #000;
        text-align: center;
    }
    .navbar-fixed-top .navbar-nav>li>a {
        color: #000;
        text-align: center;
    }
    .navmobileicon{
        display: block;
    }
}


