  color: #FFFFFF;
  background: #222222;
  -webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
          filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
} */

.wrapper {
  max-width: 1335px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 20px;
  margin: auto;
}

.wrapper a {
  display: inline-block;
  margin: 5px;
}

.wrapper a:first-child {
  margin-left: 0;
}

.slider-wrapper {
  position: relative;
  height: 700px;
  overflow: hidden;
}

.slide {
  height: 700px;
  background-size: cover !important;
}

.slide::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.5)));
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
    background: -o-linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.5));
    bottom: 0;
    left: 0;
}

.previousButton, .nextButton {
    display: block;
		position: absolute;
		bottom: 20%;
		-webkit-transform: translateY(-50%);
		    -ms-transform: translateY(-50%);
		        transform: translateY(-50%);
		z-index: 10;
		background: url('/images/rightarr.png') no-repeat center center / 50px;
		width: 100px;
		height: 50px;
		text-indent: -9999px;
		cursor: pointer;
}

/* .previousButton:hover, .nextButton:hover {
  background: url('http://svgshare.com/i/41_.svg') no-repeat center center / 16px;
} */

.previousButton {
  right: 130px;
  -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
          -ms-transform: rotate(180deg) translateY(calc(50% + 0px));
      transform: rotate(180deg) translateY(calc(50% + 0px));
}

/* .previousButton:hover {
  left: -10px;
} */

.nextButton {
  right: 50px;
}

/* .nextButton:hover {
  right: -10px;
} */

.slider-content {
  text-align: left;
  background-attachment: fixed !important;
}

.slider-content .inner {
  padding: 0 120px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 55%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
}

.slider-content .inner button{
  -webkit-transition-delay: 0s;
          -o-transition-delay: 0s;
     transition-delay: 0s;
}

.slider-content .inner h2 {
  margin: 0 auto;
  /* max-width: 840px; */
  color: #FFFFFF;
  font-size: 60px;
  line-height: 1;
}

.slider-content .inner p {
  color: #FFFFFF;
  font-size: 18px;
  line-height: 1.5;
  margin: 20px auto 30px;
  /* max-width: 640px; */
}

.slider-content section {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.slider-content section span {
  color: #FFFFFF;
}

.slider-content section span {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  display: inline-block;
  text-align: left;
  line-height: 1.4;
  vertical-align: middle;
  margin-left: 10px;
}

.slider-content section img {
  width: 40px;
  height: 40px;
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  vertical-align: middle;
}

.slider-content section span strong {
  color: #FFFFFF;
  font-size: 14px;
  display: block;
}

/* @media (max-height: 500px) {
  .slider-wrapper, .slide {
    height: calc(100vh - 75px);
  }    
} */

@media only screen and (max-width: 667px) {
  .webcarousel{
    display: none;
  }
  .mobilecarousel{
    display: block;
  }
}

@media (max-width: 640px) {
  .slider-wrapper, .slide {
    height: calc(80vh - 75px);
  }    
}

@media (max-height: 600px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }    
}

@media (max-width: 640px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }
}


