@font-face {
	font-family: lucida;
	src: url('/font/lucidacalligraphyitalic.ttf');
}

@font-face {
	font-family: DINLight;
	src: url('/font/DINLight.otf');
}

@font-face {
	font-family: oswald;
	src: url('/font/GeorgiaRegularFont.TTF');
}

@font-face {
	font-family: Great;
	src: url('/font/GreatVibes-Regular.otf');
}

.great{
	font-family: lucida !important;
}

p{
	font-size: 1.1em;
	letter-spacing: 0.1em;
    font-family: oswald;
    font-style: italic;
    text-align: center;
    line-height: 1.5em;
    text-align: justify;
}
h1{
	font-family: lucida !important;
}
h2{
	font-family: lucida !important;
}
h3{
	font-family: lucida !important;
}
label{
	font-family: oswald;
	color: #888;
	font-size: 1em;
}
h4{
	font-family: DINlight;
}
span{
 font-family: oswald;
}

.container .row {
	padding: 20px 30px;
}

a:hover{
	text-decoration: none;
}

.switch-wrapper {
	position: relative;
}

 /* .switch-wrapper > div {
     position: absolute;
 } */

.mainmargin {
	margin-top: -40px !important;
}

.newbookroombtn{
	height: 45px;
	border: 2px solid #e4572e;
	background-color: #e4572e;
	color: #fff;
	font-family: DINlight;
	width: 50%;
	font-size: 1.6em;
}

.newbookroombtn:hover{
	border: 2px solid #e4572e;
	background-color: #fff;
	color: #e4572e;
	-webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
	text-decoration: none !important;
}

.roomimgopa:hover  {
	opacity: 0.5;
	filter: alpha(opacity=50); /* For IE8 and earlier */
}

.roomimgopa{
	width: 100%;
	height: 450px;
	opacity: 1.0;
	filter: alpha(opacity=100); /* For IE8 and earlier */
}

.roompadtop{
	margin-top: 100px;
}

.footemailinput{
	border: 1px solid #fff;
    border-radius: 0;
    height: 50px;
    background-color: #cda05f;
}

.footemailinput::-webkit-input-placeholder {
	color: #fff !important;
}

.footemailinput::-ms-input-placeholder {
	color: #fff !important;
}

.footemailinput::placeholder {
	color: #fff !important;
}

.footemailinput:focus{
	border: 1px solid #fff;
	-webkit-box-shadow: none;
	        box-shadow: none;
}
.footemailbtn{
	height: 40px;
    width: 100px;
    border: 1px solid #e4572e;
    text-transform: uppercase;
    background-color: #e4572e;
    color: #fff;
}
.footemailbtn:hover{
    background-color: #fff;
	color: #e4572e;
	-webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
	text-decoration: none !important;
}

.roomdescdiv{
	min-height: 110px;
    width: 70%;
    background-color: rgb(255, 255, 255);
    -webkit-box-shadow: rgb(153, 153, 153) 3px 2px 6px;
            box-shadow: rgb(153, 153, 153) 3px 2px 6px;
    right: 0px;
    margin-top: -40px;
    position: absolute;
}

.roomdetaildiv{
	padding: 10px 9px;
}

.roompadding{
	height: 650px;
}

/* .mainmodal{
	width: auto;
	margin: 0% 0%;
}

.mainmodalcross{
	margin-top: 10px;
	margin-right: 12px;
	font-size: 3.5em;
} */

.mobileview{
	display: none;
}
.webview{
	display: block;
}

.aboutoverlay{
	width: 100%;
	height: 600px;
	position: absolute;
	z-index: 10;
	background-color: rgba(0,0,0,0.3);
	padding-top: 20%;
	padding-left: 10%;
}

.bookmarkRibbon {  
	width: 50%;
	height: 50px;
    background-color: #e4572e;
    min-height: 40px;
    color: #fff;
    font-size: 1.2em;
    overflow: hidden;        /* Increase element Width */
}

.bookmarkRibbon:hover{  
	border: 2px solid #e4572e;
    background-color: #fff;
	color: #e4572e;  
	-webkit-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;    /* Increase element Width */
}
.allroomsdiv{
	height: 600px;
	width: 100%;
	background-attachment: fixed !important;
	background-size: cover !important;
}

.abouttext {
    -webkit-box-shadow: rgba(0,0,0,.4) 0 0 5px;
            box-shadow: rgba(0,0,0,.4) 0 0 5px;
    background-color: rgba(255, 255, 255, 0.82);
    min-height: 460px;
    padding: 5px 50px;
}

.webview{
	display: block;
}

.mobileview{
	display: none;
}

.aboutoverlay h1{
	color: #fff;
	font-size: 4em;
}

.aboutoverlay p{
	color: #fff;
	font-size: 1.4em;
}

.roomdetaildiv h3{
	font-size: 1.7em;
	color: rgb(10, 50, 93);
	text-align: left;
}

.scrolltotop{
	background-color: #0000008f;
	padding: 0px 13px;
}

.scrolltotop span{
	font-size: 2.5em;
	color: #ffffff;
}

.roomsliderimg{
	width: 100%;
	height: 400px;
}

.roomsdescpad{
	margin-top:5px;
	padding:30px 50px;
}

.roomsdescpad h3{
	color:rgb(0, 44, 92);
	margin-top:0px;
	text-align:left;
	font-size:2.3em;
}
.roomsdescpad span{
	font-size: 2.8em;
	color: #cda05f;
}

.aboutdivideleft{
	padding-left:115px;
	padding-top: 25px;
}

.aboutdivideright{
	padding-right:115px;
	padding-top: 25px;
}


@media only screen and (max-width: 1000px) {
	.webview{
		display: none;
	}
	
	.mobileview{
		display: block;
	}
}

@media only screen and (max-width: 667px) {
	.abouttext {
		padding: 5px 15px;
	}
	.aboutdivideleft{
		padding-left:15px;
	}
	
	.aboutdivideright{
		padding-right:15px;
	}
	.roomsdescpad span{
		font-size: 2em;
		color: #cda05f;
	}
	.roomsdescpad h3{
		font-size:1.3em;
	}
	
	.roomsdescpad{
		padding:10px 10px;
	}
	.roomsliderimg{
		width: 100%;
		height: 200px;
	}
	.roomdetaildiv h3{
		font-size: 1.3em;
	}
	.roomdescdiv{
		width: 100%;
		margin-top: 0px;
	}
	p{
		letter-spacing: 0em;
	}
	.aboutoverlay , .allroomsdiv{
		height: 350px;
	}

	.aboutoverlay{
		padding-top: 50%;
	}

	.aboutoverlay h1{
		color: #fff;
		font-size: 2em;
	}
	.roomimgopa{
		height: 250px;
	}
	.roompadding{
		height: 460px;
		padding: 0px;
	}
	.mobileview{
		display: block;
	}
	.webview{
		display: none;
	}
	.newbookroombtn{
		width: 100%;
	}
}


